import React from 'react';



const CommentList = ({comment}) => {
    return (
        <div>
            <h3>Comments</h3>
            {comment.map((comment,key)=>(
                <div key={key}> 
                    <h4>{comment.username}</h4>
                    <p>{comment.text}</p>


                </div>
            ))}

            
        </div>
    )
}

export default CommentList 
