import React, { useState,useEffect } from 'react';
import ArticleContent from './Article-content';
import ArticleList from '../components/ArticleList';
import NotFoundPage from './NotFoundPage';
import 'whatwg-fetch';
import CommentList from '../components/CommentList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';


const Article = ({match}) => {

        const [articleInfo,setArticleInfo]= useState({upvotes:0,comments:[]});
        const name = match.params.name;
        useEffect(()=>{

            //setArticleInfo({upvotes:Math.ceil(Math.random()*10)});

            const fetchData= async()=>{
                const result= await fetch(`/api/article/${name}`);
                const body = await result.json();
                setArticleInfo(body);
                
                
                
            }

            fetchData();
        },[name])
    
        
        const Articles = ArticleContent.find(Article => Article.name === name);

        if (!Articles) return <NotFoundPage />
        const otherArticles = ArticleContent.filter(Article => Article.name !== name);

        return (
            <div>
                <p>Article has been upvoted {articleInfo.upvotes} times</p>
                <h1>This is the {Articles.title} article</h1>
                <UpvotesSection ArticleName={name} upvotes={articleInfo.upvotes}
                setArticleInfo={setArticleInfo} />
                {Articles.content.map((paragraph, key) => (
                    <p key={key}>{paragraph}</p>
                ))}

                <AddCommentForm ArticleName={name} setArticleInfo={setArticleInfo}/>

                <CommentList comment={articleInfo.comments}/>
                <h3>Other Articles</h3>
                <ArticleList Article={otherArticles} />
            </div>
        );
    
}

export default Article


